import sketch from 'sketch'
var document = require('sketch/dom').getSelectedDocument()

// documentation: https://developer.sketchapp.com/reference/api/
export default function(){
  sketch.UI.message('This plugin works when you collapse groups')

}

export function onCollapse() {
console.log('Collapsing')

//var doc = require('sketch/dom').getSelectedDocument()


var arr = []
for(var i = 0; i < document.sketchObject.selectedLayers().layers().length; i++){
  arr.push(document.sketchObject.selectedLayers().layers()[i])
}
document.sketchObject.currentPage().changeSelectionBySelectingLayers(nil);
document.sketchObject.currentPage().changeSelectionBySelectingLayers(arr);
}
